html {
	scroll-behavior: smooth;
}

  .scroll-down-section {
	display: flex;
	justify-content: center;
	align-items: center;
	padding-bottom: 25vh;
  }

  .scroll-down-icon {
	position: absolute;
	font-size: 30px;
	bottom: 16vh;
	color: #fff;
	transition: all 0.2s ease-out;
  }

  .scroll-down-icon:hover {
	opacity: 0.4;
  }
  