.cards {
	padding: 4rem;
	background: #fff;
  }
  
  h1 {
	text-align: center;
  }
  
  .cards__container {
	display: flex;
	flex-flow: column;
	align-items: center;
	/* max-width: 1120px; */
	width: 90%;
	margin: 0 auto;
  }
  
  .cards__wrapper {
	position: relative;
	margin: 50px 0 45px;
  }
  
  .cards__items {
	margin-bottom: 24px;
  }
  
  .cards__item {
	display: flex;
	flex: 1;
	margin: 0 1rem;
	border-radius: 10px;
  }
  
  .cards__item__link {
	display: flex;
	flex-flow: column;
	width: 100%;
	box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
	/* -webkit-filter: drop-shadow(0 6px rgba(56, 125, 255, 0.017)); */
	/* filter: drop-shadow(0 6px rgba(56, 125, 255, 0.017)); */
	border-radius: 10px;
	overflow: hidden;
	text-decoration: none;
  }
  
  .cards__item__pic-wrap {
	position: relative;
	width: 100%;
	padding-top: 67%;
	overflow: hidden;
  }
  
  .cards__item__pic-wrap::after {
	content: attr(data-category);
	position: absolute;
	bottom: 0;
	margin-left: 10px;
	padding: 3px 8px 6px 8px;
	max-width: calc((100%) - 50px);
	font-size: 16px;
	font-weight: 600;
	color: #fff;
	background-color: rgb(49, 50, 50);
	box-sizing: border-box;
	border-radius: 8px 8px 0 0;
	font-family: 'Roboto Slab', 'Nunito', sans-serif;
  }
  
  .cards__item__img {
	position: absolute;
	top: 0;
	right: 0;
	display: block;
	height: 100%;
	/* width: 100%;
	max-width: 100%;
	max-height: 100%; */
	object-fit: cover;
	transition: all 0.2s ease;
	z-index: 0;
  }

  .cards__item__link:hover .cards__item__img {
	transform: scale(1.1);
  }

  .cards__item__info {
	padding: 20px 30px 30px;
  }
  
  .cards__item__text {
	color: rgb(52, 52, 52);
	font-size: 18px;
	line-height: 24px;
  }
  
  @media only screen and (min-width: 960px) {
	.cards__items {
	  display: flex;
	}
	.cards__wrapper {
		min-width: 75vw;
	}
  }
  
  @media only screen and (max-width: 960px) {
	.cards__item {
	  margin-bottom: 2rem;
	}
	.cards__item {
		min-width: 65vw;
	}
  }

  .cards__item__pic-wrap__simple {
	position: relative;
	padding-top: 67%;
	overflow: hidden;
  }

  .cards__item__pic-wrap__simple::after {
	content: attr(data-category);
	position: absolute;
	/* bottom: calc(48% - 0.5em);
	left: 10%; */
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	max-width: 100%;
	font-size: 375%;
	font-weight: 700;
	color: #fff;
	background-color: rgba(103, 81, 47, 0.25);
	border-radius: 10px;
	box-sizing: border-box;
	font-family: 'Roboto Slab', sans-serif;
}

.cards__item__pic-wrap__named {
	position: relative;
	padding-top: 67%;
	overflow: hidden;
}

.cards__item__pic-wrap__named::after {
	content: attr(data-category);
	position: absolute;
	padding: 6px 8px;
	max-width: calc((100%) - 50px);
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	font-size: 20px;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	font-weight: 600;
	color: #fff;
	background-color: rgb(37, 38, 39);
	box-sizing: border-box;
	border-radius: 10px;
	opacity: 0.7;
	font-family: 'Roboto Slab', 'Nunito', sans-serif;
	transition: all 0.2s ease;
}

.cards__item__link:hover .cards__item__pic-wrap__named::after {
	opacity: 1;
	font-size: 26px;
	max-width: calc((100%) - 10px);
	left: 50%;
	transform: translate(-50%, -90%);
}

/* Used for Cmd-Fs */
.hidden {
	opacity: 0;
}
